section.approval {
  .approval-content {
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1240px;
    h3 {
      font-size: 3.0rem;
      text-align: center;
      color: $sub-text; }
    .approval-grid {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(370px, 1fr));
      grid-gap: 50px 50px;
      margin-top: 30px;
      width: 80%;
      justify-content: center;
      .approval-item {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        img {
          height: 90px;
          margin-right: 20px; }
        .text-container {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          h4 {
            margin: 0;
            font-size: 1.3rem;
            color: $sub-text; }
          p {
            font-size: 1.2rem;
            color: $sub-text-light;
            margin-top: 10px;
            font-weight: 600; } } } } } }


@media screen and (max-width: 1060px) {
  section.approval {
    .approval-content {
      padding: 100px 0;
      h3 {
        font-size: 2.5rem; } } } }

@media screen and (max-width: 600px) {
  section.approval {
    .approval-content {
      h3 {
        font-size: 2.3rem; }
      .approval-grid {
        width: 100%;
        .approval-item {
          img {
            height: 75px; }
          .text-container {
            h4 {
              font-size: 1.2rem; }
            p {
              font-size: 1.1rem; } } } } } } }

