section.cash-flow {
  background-color: $primary;
  .cash-flow-content {
    width: 80%;
    margin: 0 auto;
    max-width: 1240px;
    .text-and-photo {
      display: flex;
      justify-content: space-evenly;
      align-items: flex-end;
      margin-bottom: 30px;
      .left {
        h3 {
          font-size: 3.0rem;
          color: $bg-color; }
        img {
          height: 230px; } }
      .right {
        img {
          height: 450px; } } }
    p {
      font-size: 1.4rem;
      color: $bg-color;
      text-align: center;
      font-weight: 400;
      margin-top: 40px; } } }


@media screen and (max-width: 1060px) {
  section.cash-flow {
    .cash-flow-content {
      .text-and-photo {
        .left {
          h3 {
            text-align: center; }
          img {
            height: auto;
            width: 100%; } }
        .right {
          display: none; } } } } }

@media screen and (max-width: 600px) {
  section.cash-flow {
    .cash-flow-content {
      .text-and-photo {
        .left {
          h3 {
            font-size: 2.3rem; } } }
      p {
        font-size: 1.3rem; } } } }


@media screen and (min-width: 1300px) {
  section.cash-flow {
    .cash-flow-content {
      .text-and-photo {
        .left {
          max-width: 45%; } } } } }
