@mixin default-shadow {
  -webkit-box-shadow: 10px 25px 20px -20px  rgba(31,17,123,0.1);
  -moz-box-shadow: 10px 25px 20px -20px  rgba(31,17,123,0.1);
  box-shadow: 10px 25px 20px -20px  rgba(31,17,123,0.1); }

@mixin card-shadow {
  -webkit-box-shadow: 0px 10px 11px -1px rgba(31,17,123,0.1);
  -moz-box-shadow: 0px 10px 11px -1px rgba(31,17,123,0.1);
  box-shadow: 0px 10px 11px -1px rgba(31,17,123,0.1); }


@mixin remove-select-icon {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }
