section.offer {
  .offer-content {
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1240px;
    .amt-card-left {
      margin-right: 50px;
      .amt-card-bg {
        height: 580px;
        width: 40vw;
        background-color: $card-bg;
        border-radius: 15px;
        position: relative;
        .amt-card {
          background-color: $bg-color;
          border-radius: 15px;
          position: absolute;
          top: 40%;
          left: 50%;
          transform: translate(-50%, -43%);
          width: 85%;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;
          .amt-card-content {
            padding: 20px;
            text-align: center;
            h4 {
              font-size: 1.3rem;
              color: $sub-text; }
            h3 {
              font-size: 2.8rem;
              color: $accent; }
            input#amount-range {
              width: 100%;
              margin: 0;
              margin-bottom: 40px;
              -webkit-appearance: none;
              height: 8px;
              background-color: $accent;
              border-radius: 5px;
              &:focus {
                outline: none; }
              &::-webkit-slider-thumb {
                -webkit-appearance: none;
                background: $accent;
                height: 25px;
                width: 25px;
                border: none;
                border-radius: 50%; } }
            .fee-container {
              display: flex;
              justify-content: space-between;
              margin: 10px 0;
              color: $sub-text;
              font-weight: 500; } } }
        span#ill {
          position: absolute;
          bottom: 5%;
          left: 50%;
          transform: translate(-50%, 0);
          color: $sub-text;
          font-weight: 500; } } }
    .text-right {
      h3 {
        font-family: 'Josefin Sans';
        font-size: 2.6rem;
        margin-top: 0;
        color: $primary; }
      p {
        font-family: 'DM Sans';
        font-size: 1.3rem;
        font-weight: 500;
        color: $sub-text; } } } }


@media screen and (max-width: 1000px) {
  section.offer {
    .offer-content {
      flex-direction: column;
      padding: 100px 0;
      .amt-card-left {
        margin-bottom: 50px;
        margin-right: auto;
        width: 100%;
        .amt-card-bg {
          margin: 0 auto;
          width: 100%;
          height: 750px; } }
      .text-right {
        margin: 50px 0;
        text-align: center; } } } }

@media screen and (max-width: 600px) {
  section.offer {
    .offer-content {
      .amt-card-left {
        margin-right: auto;
        .amt-card-bg {
          width: 100%;
          height: 450px;
          .amt-card {
            .amt-card-content {
              h3 {
                font-size: 2.2rem; } } } } }
      .text-right {
        h3 {
          font-size: 2.2rem; }
        p {
          font-size: 1.4rem; } } } } }

@media screen and (min-width: 1300px) {
  section.offer {
    .offer-content {
      .amt-card-left {
        .amt-card-bg {
          height: 600px;
          width: 470px; } }
      .text-right {
        max-width: 50%;
        h3 {
          font-size: 3.0rem; } } } } }
