section.post {
  height: auto;
  .post-content {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    padding: 100px 0 50px 0;
    a#back {
      font-size: 1.2rem;
      float: left;
      width: 100%;
      margin: 0 auto 20px;
      &::before {
        content: "\27f5";
        margin-right: 5px; } }
    h3 {
      font-size: 2.5rem;
      margin: 30px auto 15px; }
    div#category {
      padding: 6px 10px;
      border-radius: 25px;
      border: 1px solid $sub-text-light;
      width: 80px;
      margin: 0 auto 30px;
      font-size: 0.9rem;
      color: $sub-text-light; }
    img {
      width: 100%;
      height: auto;
      margin: 0 auto;
      margin-bottom: 20px; }
    .article-wrapper {
      width: 100%;
      margin: 0 auto;
      text-align: left;
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
      article {
        font-size: 1.2rem;
        width: 70%; }
      div#ad {
        width: 25%;
        height: 480px;
        background-color: lightgray; } } } }

@media screen and (max-width: 640px) {
  section.post {
    .post-content {
      h3 {
        font-size: 2.1rem; } } } }


@media screen and (min-width: 1280px) {
  section.post {
    .post-content {
      h3 {
        width: 50%; }
      img {
        width: 80%; }
      .article-wrapper {
        width: 80%; } } } }
